@import "reset.css";
@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,700');
* {
    box-sizing: border-box;
}
body {
    background-color: #f6f6f9;
    font-family: 'Open Sans', sans-serif, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
        Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    font-size: 15px;
    color: #2a190b;
    font-weight: 500;
}
.phone{
    margin-top: 50px;
    margin-left: 50px;
    background-color: #f5f6f8;
    width: 350px;
    height: 700px;
    border-radius: 40px;
    box-shadow: inset -6px -6px 15px rgba(145, 160, 180, 0.45),
        30px 30px 40px rgba(118, 146, 180, 0.18);
    overflow: hidden;
    padding: 20px;
}
.screen {
    width: 100%;
    height: 100%;
    background-image: linear-gradient(#f8f8f8, #e3e6e8);
    border-radius: 25px;
    position: relative;
    overflow: hidden;
}
.header{
    margin-top: 25px;
    padding: 0 15px 0 15px;
    display: flex;
    justify-content: space-between;
    font-size: 17px;
}
.main{
    margin-top: 20px;
    padding: 0 15px 0 15px;
}
.popular-recipes__header{
    display: flex;
    justify-content: space-between;
}
.popular-recipes__header .header__column .title{
    font-size: 15px;
    font-weight: 600;
}
.popular-recipes__header .header__column .view_all{
    font-size: 13px;
    font-weight: 500;
    color:red;
}
.popular-recipes__category{
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
}
.popular-recipes__category .gategory-box{
    width: 45%;
    height: 90px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10%;
    font-size: 13px;
    color: #f1f1f1;
    margin-top: 13px;
}
.gategory-box:first-child{
    background-image: url("https://proxy.duckduckgo.com/iu/?u=https%3A%2F%2Flivenaturallymagazine.com%2Fwp-content%2Fuploads%2F2016%2F12%2Fcooking-at-home.jpg&f=1");
    background-size: cover;
}
.gategory-box:nth-child(2){
    background-image: url("https://proxy.duckduckgo.com/iu/?u=https%3A%2F%2Fi.ytimg.com%2Fvi%2Fx5X3CDPXxgk%2Fmaxresdefault.jpg&f=1");
    background-size: cover;
}
.gategory-box:nth-child(3){
    background-image: url("https://proxy.duckduckgo.com/iu/?u=https%3A%2F%2Fwww.archanaskitchen.com%2Fimages%2Farchanaskitchen%2F9-Baby_Todder%2FCarrot_Puree_Recipe_For_Babies-1.jpg&f=1");
    background-size: cover;
}
.gategory-box:last-child{
    background-image: url("https://proxy.duckduckgo.com/iu/?u=http%3A%2F%2Fafewdollarsmore.com%2Fwp-content%2Fuploads%2F2015%2F03%2Fsmoothies-2.jpg&f=1");
    background-size: cover;
}
.other-recipes{
    margin-top: 20px;
}
.other-recipes__header .header__column{
    font-size: 15px;
    font-weight: 600;
}
.other-recipes__lists{
    margin-top: 13px;
}
.other-recipes__list{
    margin-top:13px;
    padding: 0 10px 0 10px;
    width: 100%;
    height: 55px;
    background-color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 10px;
}

.other-recipes__list .list__info h4{
    font-size: 13px;
}
.other-recipes__list .list__info h5{
    font-size: 11px;
    color: #979797;
    margin-top: 3px;
}
.other-recipes__list .btn_right{
    color: red;
}
.footer{
    position: absolute;
    bottom: -5px;
    background-color: white;
    height: 60px;
    width: 100%;
    border-radius: 5px;
    box-shadow: 5px 5px 40px rgba(118, 146, 180, 0.5);
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-size: 20px;
}